import { lazy, memo } from 'react';
import './About.css';
import AboutPhoto from '../../assest/images/about.jpg';
const SectioTitle = lazy(()=>import('../../component/SectionTitle/SectionTitle')); 
const About = () => {
    return (
        <>
            <div className='about mt-20 mb-10' id='about'>
               <SectioTitle title="About Company"/>
                <div className='container'>
                    <div className='row mt-10 flex-wrap'>
                        <div className='about-image'>
                            <img src={AboutPhoto} alt='about-company' width={'90%'} height={"100%"} className='rounded' />
                        </div>
                        <div className='about-caption'>
                            <h1 className='text-2xl font-bold text-slate-900 mb-2'>About Our Copmany</h1>
                            <p className='text-sm '>ShopNest is a forward-thinking e-commerce company dedicated to revolutionizing the way people shop online. With a customer-centric approach, ShopNest aims to create a seamless and personalized shopping experience by offering a wide range of high-quality products at competitive prices. The company prides itself on its innovative use of technology, including AI-driven recommendations and user-friendly interfaces, to ensure that every customer finds exactly what they need. ShopNestâ€™s vision is to become a trusted global marketplace that not only meets but exceeds customer expectations, fostering long-term loyalty and satisfaction. By prioritizing sustainability and ethical practices, ShopNest is committed to making a positive impact on both the environment and the communities it serves.

                            </p>
                            <button className='block mt-5'><a className='bg-red-600 p-3 rounded-md text-white' href='/EcommerceSite' title='Our view and politices'>Read More</a></button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default memo(About);